<div><v-layout><v-app-bar id="color" absolute style="overflow: visible"><v-app-bar-nav-icon @click="clickdrawer()"></v-app-bar-nav-icon><v-app-bar-title><p>Mes compétences</p></v-app-bar-title></v-app-bar><v-navigation-drawer class="overflow-" id="navigationdrawer" v-model="drawer" absolute temporary><v-list nav dense><div v-for="item in redirectitems"><v-list-item :prepend-icon="item.icon" :title="item.title" @click="redirectnavbar(item.path)"></v-list-item></div></v-list></v-navigation-drawer><v-main><v-card class="mx-auto overflow-hidden" height="100vh" width="0vw"></v-card></v-main><div id="main"><v-card id="ecartcategorie" elevation="0"></v-card><v-card id="ecartcategorie" elevation="0"></v-card><div style="width: 90vw"><a id="white" href="../assets/CV.pdf" download="CV.pdf">Télécharger mon CV</a><br><br><br><v-card style="width: 70vw"><v-card-title><a>Mon projet professionnel</a></v-card-title><v-card-text><p>Devenir développeur logiciel, bien sur !</p><br><p>Et partir d'entrée de jeu en alternance jusqu'à bac+2 / 3, pour commencer.</p><p>Mobile France entière: C'est l'environnement professionnel et les missions qui m'intéressent :</p><p>Parce qu'il n'y a rien de mieux que l'entreprise pour appliquer les apprentissages de l'école, pratiquer, s'immerger dans le monde professionnel,<a> travailler en équipe, échanger des points de vue, et surtout s'impliquer dans des projets concrets et utiles.</a></p><p>Et voir du pays, c'est un plus ! ;-)</p><br><p>Continuer jusqu'au master, sans doute, dans une orientation client applicatif, probablement en .NET.</p><br><p>Mais chaque chose en son temps ^^ !</p></v-card-text></v-card><v-card id="ecartcategorie" elevation="0"></v-card><v-card style="width: 70vw"><v-card-title><p>Mes Softskills</p></v-card-title><v-card-text><lu><li>Curieux : Si c'est utile à mon projet ou ma mission, ça m'intéresse !!</li><li>Autonome : Pour mes formations, je me suis renseigné, j'ai suivi des tutoriels, trouvé des projets pour appliquer.</li><li>Esprit d'équipe : L'alternance c'est aussi sortir du cadre scolaire et intégrer une équipe professionnelle, et nouer des relations de travail pour travailler en synergie. </li><li>Tenace : En difficulté, je cherche, je m'accroche et je me dépasse.</li><li>Flexible : Je m'adapte aux situations et environnements, j'aime quand ca bouge (et quoi de mieux que l'informatique pour ça) !</li></lu></v-card-text></v-card><v-card id="ecartcategorie" elevation="0"></v-card><v-card style="width: 70vw"><v-card-title><p>Mes connaissances informatiques actuelles</p></v-card-title><v-card-text><p>J'ai à peine 18 ans, je suis loin d'avoir 10 ans d'expérience, mais je ne pars pas de rien (voir <a id="link" @click="redirectnavbar('/projects')">réalisations et projets</a><a>) ^^ !</a></p></v-card-text><v-card elevation="0"><v-card-title><p>Langages</p></v-card-title><v-card-text><p>Python<img href="./../assets/3.png" alt="3"></p><p>HTML / CSS</p><p>SQL</p><p>C#</p></v-card-text></v-card><v-card elevation="0"><v-card-title><p>Environnements</p></v-card-title><v-card-text><p>Visual Studio Code</p><p>Visual Studio</p><p>PGAdmin 4</p><p>PhPMyAdmin</p><p>SQLite Studio</p></v-card-text></v-card><v-card elevation="0"><v-card-title><p>Plateformes / Frameworks</p></v-card-title><v-card-text><p>Flask</p><p>Vue.JS</p><p>.NET</p></v-card-text></v-card><v-card elevation="0"><v-card-title><p>Autre</p></v-card-title><v-card-text><p>GitHub</p><p>Docker</p><p>Kubernetes</p><p>Packoffice</p></v-card-text></v-card></v-card></div></div></v-layout></div>